.sections {
    width: 100%;
    height: calc(100vh - 70px);
    background-color:salmon;
    position: relative;
    top: 70px;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory ;
    scrollbar-width: none;
   
  }
  .sections ::-webkit-scrollbar{
    display: none;
}
 .sections>* {
    width: 100vw;
    height: calc(100vh - 70px);
    scroll-snap-align: start;
}